export const trackMouse = () => {
  window.mouseX = 0;
  window.mouseY = 0;

  document.getElementById('canvas').addEventListener('mousemove', e => {
    window.mouseX = e.x - e.target.offsetLeft;
    window.mouseY = e.y - e.target.offsetTop;
  });
}

export const interpolate = (t, n1, n2) => {
  return ((n2 - n1) * t) + n1;
};

export const randomBetween = (min, max) => {
  return ((max - min) * Math.random()) + min;
}

export const mapRange = (v, r1l, r1u, r2l, r2u) => {
  return r2l + (r2u - r2l) * (v - r1l) / (r1u - r1l);
}

export const distance = (x1, y1, x2, y2) => {
  let a = x1 - x2;
  let b = y1 - y2;

  return Math.sqrt(a * a + b * b);
}

export const clamp = (value, max, min = 0) => {
  return Math.min(Math.max(value, min), max);
}

export const shuffle = (array) => {
  let _arr = array.slice();
  for (let i = _arr.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    [_arr[i], _arr[j]] = [_arr[j], _arr[i]];
  }

  return _arr;
}


export const color = (r, g, b) => {
  return { r, g, b };
}

export const colorToString = ({ r, g, b }) => {
  return `rgb(${ r }, ${ g }, ${ b })`;
}

export const colourInterpolate = (t, color1, color2) => {
  return color(
    interpolate(t, color1.r, color2.r),
    interpolate(t, color1.g, color2.g),
    interpolate(t, color1.b, color2.b),
  );
}

export const getPointFromAngleRadius = (angle, radius, x = 0, y = 0) => {
  return {
    x: x + (Math.cos(angle) * radius),
    y: y + (Math.sin(angle) * radius)
  };
}



/**
 * Creates an array of stepped colours through an array of colours
 *
 * @param {Array.<{ r: number, g: number, b: number }>} colours
 * @param {number} granularity
 *
 * @returns {Array.<{ r: number, g: number, b: number }>}
 */
export const getInterpolatedColours = (colours = [], granularity = 100) => {
  let colourStop = 1 / (colours.length - 1);
  let granularityStop = 1 / granularity;

  let colourArr = [];

  for (let i = 0; i <= 1; i += granularityStop) {
    let t = (i % colourStop) / colourStop;
    let currColourIdx = Math.floor(i / colourStop);
    colourArr.push(colourInterpolate(t, colours[currColourIdx], colours[currColourIdx + 1]));
  }

  return colourArr;
}



/**
 * Finds what would be the most relevant colour
 * in a colour array most likely created by
 * getInterpolatedColours
 *
 * @param {number} t
 * @param {Array.<{ r: number, g: number, b: number }>} colourArr
 *
 * @returns {{ r: number, g: number, b: number }}
 */
export const getClosestColour = (t, colourArr) => {
  return colourArr[Math.round((colourArr.length - 1) * t)];
}


/**
 * Loads an image as an element
 *
 * @param {String} src
 */
export const loadImage = src => {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = () => {
      resolve(img);
    };
    img.src = src;
  });
}


/**
 * Draw an image centered within an area
 *
 * @param {ImageElement} img
 * @param {Number} x
 * @param {Number} y
 * @param {Number} width
 * @param {Number} height
 * @param {Number} imgWidth
 * @param {Number} imgHeight
 */
export const drawCenteredImage = (img, x, y, width, height, imgWidth, imgHeight) => {
  let newWid, newHei;

  if (imgWidth > imgHeight) {
    newHei = height;
    newWid = (height / imgHeight) * imgWidth;
  } else {
    newWid = width;
    newHei = (width / imgWidth) * imgHeight;
  }

  let xOffset = (newWid - width) / 2;
  let yOffset = (newHei - height) / 2;

  ctx.drawImage(img, x - xOffset, y - yOffset, newWid, newHei);
}


/**
 * Draws the text centered within an area to its best ability
 *
 * Line height is definitely the biggest thing to play with
 *
 * @param {String} text
 * @param {Number} x
 * @param {Number} y
 * @param {Number} width
 * @param {Number} height
 * @param {Number} lineHeight
 */
export const drawCenteredText = (text, x, y, width, height, lineHeight) => {
  const { width: textWidth } = ctx.measureText(text);

  // let xOffset = (newWid - width) / 2;
  // let yOffset = (newHei - height) / 2;
  let xOffset = (width - textWidth) / 2;
  let yOffset = (height / 2) + (lineHeight / 2);

  ctx.fillText(text, x + xOffset, y + yOffset);
}


/**
 * Returns brightness according to 'HSP'
 *
 * @param {color} Color like object
 *
 * @returns {Number} 0-255
 */
export const getBrightness = ({ r, g, b }) => {
  return Math.sqrt(
    0.299 * (r * r) +
    0.587 * (g * g) +
    0.114 * (b * b)
  );
}
