const media = {
  el: false,
  width: 0,
  height: 0
};

export const getMediaInfo = () => media;

export const getWebcam = () => {
  return navigator.mediaDevices.getUserMedia({
    audio: false,
    video: true
  });
}

export const createVideo = (stream) => {
  return new Promise((resolve, reject) => {
    const videoEl = document.createElement('video');
    videoEl.srcObject = stream;
    videoEl.style.position = 'fixed';
    videoEl.style.opacity = '0';
    videoEl.style.pointerEvents = 'none';
    videoEl.style.transform = 'scale(0)';
    document.body.appendChild(videoEl);

    media.el = videoEl;

    const onCanPlay = () => videoEl.play();
    videoEl.addEventListener('canplay', e => {
      media.width = e.target.videoWidth;
      media.height = e.target.videoHeight;
      onCanPlay();
      videoEl.removeEventListener('canplay', onCanPlay);

      resolve(videoEl);
    });
  });
}

export const initialiseWebcam = () => {
  return getWebcam().then(createVideo);
}
