import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { initialiseWebcam, getMediaInfo } from './utils/camera';
import { loadImage, drawCenteredImage, drawCenteredText, getBrightness, colorToString } from './utils/utils';
import imgSrc from '../images/image.jpg';

const WIDTH = 480;
const HEIGHT = 480;

const BG = 'blue';
const FG = 'white';

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT,
  pixelRatio: 1
});

const tileSize = Math.ceil(WIDTH / 60);

const fontSize = tileSize * 1.2;
const lineHeight = fontSize * 0.7;

// const pixels = ['▁','▂','▃','▄','▅','▆','▇','█'];
const pixels = ['.', '-', '_', '@'];

const getPixel = perc => {
  return pixels[Math.floor(pixels.length * perc)];
}

const boxChars = {
  tl: '┏',
  h: '━',
  tr: '┓',
  v: '┃',
  br: '┛',
  bl: '┗',
}

const drawText = (text, x, y) => {
  const chars = text.split('');
  chars.unshift(boxChars.v);
  chars.push(boxChars.v);

  const topRow = new Array(chars.length).fill(0).map((c, idx) => {
    if (idx === 0) {
      return boxChars.tl;
    }

    if (idx === chars.length - 1) {
      return boxChars.tr;
    }

    return boxChars.h;
  })

  const bottomRow = new Array(chars.length).fill(0).map((c, idx) => {
    if (idx === 0) {
      return boxChars.bl;
    }

    if (idx === chars.length - 1) {
      return boxChars.br;
    }

    return boxChars.h;
  })

  const texts = [topRow, chars, bottomRow];

  ctx.save();
  ctx.translate(x * tileSize, (y - 1) * tileSize);

  for (let _y = 0; _y < texts.length; _y++) {
    for (let _x = 0; _x < chars.length; _x++) {
      ctx.fillStyle = BG;
      ctx.fillRect(_x * tileSize, _y * tileSize, tileSize, tileSize);

      ctx.fillStyle = FG;
      drawCenteredText(texts[_y][_x], _x * tileSize, _y * tileSize, tileSize, tileSize, lineHeight);
    }
  }

  ctx.restore();
}

const createTextObject = (text, x = 0, y = 0) => {
  return { text, x, y };
}

const textObjs = [
  createTextObject('Hi, how are you?', 0, 1),
  createTextObject('Thats great, me too!', 0, 1),
  createTextObject('Definitely, lets grab a beer', 0, 1),
  createTextObject('Great to see you', 0, 1),
];

// loadImage(imgSrc)
initialiseWebcam()
  .then(el => {
    ctx.font = `bold ${fontSize}px Helvetica`;

    const { width: videoWidth, height: videoHeight } = getMediaInfo();

    app.onRender(({ frame, width, height, debug }) => {
      ctx.clearRect(0, 0, width, height);
      drawCenteredImage(el, 0, 0, width, height, videoWidth, videoHeight);

      const pixels = ctx.getImageData(0, 0, width, height);

      const cols = width / tileSize;
      const rows = height / tileSize;

      ctx.fillStyle = BG;
      ctx.fillRect(0, 0, width, height);

      for (let x = 0; x < cols; x++) {
        for (let y = 0; y < rows; y++) {
          let _x = x * tileSize;
          let _y = y * tileSize;

          let idx = (((_y * width) + _x) * 4);

          const r = pixels.data[idx];
          const g = pixels.data[idx + 1];
          const b = pixels.data[idx + 2];

          const brightness = getBrightness({ r, g, b });

          ctx.fillStyle = FG;
          const char = getPixel(brightness / 255);
          drawCenteredText(char, _x, _y, tileSize, tileSize, lineHeight);

          // ctx.fillStyle = `rgb(${ brightness }, ${ brightness }, ${ brightness })`;
          // ctx.fillStyle = colorToString({ r, g, b });
          // ctx.fillRect(_x, _y, tileSize, tileSize);
        }
      }

      textObjs.forEach(({ text, x, y }) => {
        drawText(text, x, y);
      });

      if (frame % 60 === 0) {
        textObjs.forEach(obj => {
          obj.x = Math.floor(Math.random() * cols);
          obj.y = Math.floor(Math.random() * rows);
        });
      }
    });

    app.start();
  })
